body
	font-family: Arial, sans-serif
	font-size: 12px
	line-height: 1.4
	
// common blocks
.out
	height: 100%

.section
	height: 100%
	+g(red,#fff)

.header
	position: fixed
	top: 0
	left: 0
	right: 0
	height: 30px
	font-size: 13px
	background: #fff
	z-index: 200